.home-page {
    width: 100%;
    height: 100%;
    margin-top: 20px;
    .post {
        padding: 10px 10px;
        position: relative;
        border-radius: 8px;
        margin-bottom: 20px;
        .post-content {
            .text-content {
                .question {
                    padding: 5px 10px;
                    position: relative;
                    height: auto;
                    .vertical-bar {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 4px;
                        height: 100%;
                        border-radius: 5px;
                    }
                    .username {
                        @extend %Poppins-font;
                        font-weight: bold;
                        margin:0 5px;
                        font-size: 12px;

                        i {
                            margin-right: 5px;
                        }
                    }
                    .time {
                        font-size: 12px;
                        position: relative;
                        padding: 0 7px;
                        &:before {
                            content: '';
                            position: absolute;
                            left:0;
                            top: 50%;
                            width: 3px;
                            height: 3px;
                            border-radius: 50%;
                        }
                    }
                    .message {
                        text-align: right;
                        @extend %arabic-font;
                        font-weight: bold;
                        font-size: 16px;
                    }
                }
                .reply {
                    padding: 5px 10px;
                    position: relative;
                    height: auto;
                    img {
                        width: 40px;
                        margin-right: 15px;
                    }
                    .username {
                        @extend %Poppins-font;
                        font-weight: bold;
                        font-size: 14px;
                        margin: 0 5px 0 0;
                    }
                    .action {
                        font-size: 10px;
                    }
                    .reply-text {
                        text-align: left;
                        @extend %arabic-font;
                        font-weight: bold;
                        font-size: 16px;
                        padding: 10px 0 10px 20px;
                        overflow-wrap:break-word;
                        margin-bottom: 20px;
                        span {
                            white-space: pre-wrap;             
                            
                        }
                    }
                }
            }
        }
        .options {
            position: absolute;
            top: 0;
            right: 10px;
        }
        .actions {
            display: flex;
            justify-content: space-around;
                & > div {
                -webkit-flex-basis: 0;
                flex-basis: 0;
                -webkit-flex-grow: 1;
                flex-grow: 1;
                text-align: center;
                cursor: pointer;
                @include trans(all,.3s,linear);
                    &.like-button {
                        .like-counter {
                            margin: 0 5px;
                        }
                    }
            }
        }
    }
    
}
