.settings {
    background: $nav-back;
    position: relative;
    border-radius: 5px;
    height: auto;
    overflow: hidden;
    padding: 10px 15px;
    @extend %Poppins-font;
    .main-column {        
        section {
            padding: 15px 0;
            header {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-pack: distribute;
                justify-content: space-around;
                background: $dark-page;
                border-radius: 5px;
                padding: 5px;
                
                .title-action {
                    -ms-flex-preferred-size: 0;
                    flex-basis: 0;
                    -webkit-box-flex: 1;
                    -ms-flex-positive: 1;
                    flex-grow: 1;
                    text-align: center;
                    cursor: pointer;
                    color: $gray;                    
                    font-weight: bold;
                    font-size: 12px;
                    padding: 5px 0;
                    &.active {
                        background: $white;
                        color: $dark-page;
                    }
                }
            }
            .message-true,.message-false {                
                @extend %Poppins-font;
                padding: 8px 0;
                text-align: center;
                margin-bottom: 5px;
                background: green;
                font-size: 14px;
                font-weight: bold;
                border-radius: 5px;
            }
            .message-false {
                background: red;
            }
            /*End Header*/
                .delete-acc {                    
                    float: right;                    
                    text-align: center;
                    text-decoration: none;
                    font-weight: bold;
                    border-radius: 5px;
                    background: $dark-page;
                    border:none;
                    color: $gray;
                    padding: 15px 20px;
                    font-size: 12px;
                }
            .profile-user {               
                form {                  
                    span {
                        position: relative;
                        display: block;
                        input[type="file"] {
                            opacity: 0;
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            cursor: pointer;
                        }
                        img {
                            width: 40px;
                            display: block;
                            margin: 0 auto;                
                        }
                        
                    }
                    .btn-submit {
                        width: 100%;
                        margin-bottom: 20px;                        
                    }
                }
            }
            .changed-btn {
                display: block;
                width: 100%;
                padding: 15px 0;
                background: none;
                border: none;
                color: $main;
                font-weight: bold;
                border-bottom: 1px solid $dark-page;
                margin-bottom: 10px;
                font-size: 12px;
                outline: inherit;
                @include trans(all,.3s,linear);
                    &:hover {
                        color: rgba($main,.7);
                    }
                }
        }
    }
}
.pop-up-setting {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba($dark-page,.9);
    z-index: 999;    
    @extend %Poppins-font;
    .exit {
        color: $white;
        font-size: 18px;
        position: absolute;
        right: 15px;
        top: 0;
    }
    .edit-user{
        @include itemCenter();
        text-align: left;
        width: 400px;
        height: auto;
        background: $nav-back;
        padding: 30px;
        border-radius:5px;
        display: none;
        @include m576 {
            width: 80%;        
        }
        .header-title {
            @extend %Poppins-font;
            background: $main;
            color:$white;
            padding: 15px 0;
            margin-bottom: 20px;
            position: relative;
            width: 100%;
            display: block;
            text-align: center;
            border-radius: 5px;
            span {
                font-size: 14px;
                font-weight: bold;
            }
            
            
        }        
        label {
            font-size: 12px;
            font-weight: bold;        
        }
        .form-control {        
            background: none;        
            margin-bottom: 20px;
            color: $gray;
            @extend %Raleway-font;                    
            font-size: 14px;        
            box-shadow: none;
            border-radius: 2px;
            border:1px solid #535353;
        }
    }
}
    