$main:#ff8f1f;
$white:#FFF;
$black:#000;
$active-color:#ff5301;
$btn-cem:#f19d00;
$dark:#111111;
$dark-page:#f1f1f1;
$gray:#595959;
$gray-link:#5d5d5d;
$nav-back:#FFF;
body {
  color: $white !important;
  background: $dark-page;
}

.navigation-bar {
  background-color: $nav-back;
    .main-navbar {
      .navbar {
        .nav-item {
          &.active {
            .nav-link {
              color: $main;
            }
          }
          .nav-link {
            color: $gray;
            &:hover {
              color:$main;
            }
            .counts-notifications,.counts-message {
              background: $main;
              color: $dark-page;

            }
          }
        }
        .nav-item.dropdown {
          .dropdown-toggle {
            &::after {
              color: $main;
            }
          }
          .dropdown-menu {
            .dropdown-item {
              &:active {
                background: $main;
              }
              &:hover {
                background: $main;
                color: $white;
              }
            }
          }
        }
      }
    }
}

.home-page {
    background: $dark-page;
    color: $white;
    .post {
        background: $nav-back;

        .post-content {
            .text-content {
                .question {
                    color: $gray;
                    .vertical-bar {
                        background: rgba($main,.5);        
                    }
                    .username {
                        color: $main;
                    }
                    .time {
                        &:before {
                            background: $main;
                        }
                    }
                }
                .reply {
                    color: $gray;

                    .username {
                        color: $main;
                    }
                }
            }
        }
        .options {            
            a {
                color: $gray;
            }
        }
        .actions {
            color: $gray;
                & > div {
                &:hover {
                    color: $main;
                }
            }
        }
    }
}

.settings {
    background: $nav-back;
    .main-column {        
        section {
            header {
                background: $dark-page;
                .title-action {
                    color: $gray;
                    &.active {
                        background: $white;    
                        color: $gray;
                    }
                }
            }
            .profile-user {               
                form {
                    .btn-submit {                                                
                        background: $dark-page;
                        color: $gray;
                    }
                }
            }
            .delete-acc {
                background: $dark-page;
                color: $gray;
            }
            
                .changed-btn {
                    color: $main;
                    border-bottom: 1px solid $dark-page;
                }
        }
    }
}
.pop-up-setting {
    background: rgba($dark-page,.9);    
    .edit-user{
        background: $nav-back;
        .header-title {
            background: $gray;            
            color:$white;

        }
        label {
            color: $gray;
        }
        .form-control {
            color: $black;
            border:1px solid #ced4da;
        }
        .btn-submit {
            background: $gray;
            color: $nav-back;
        }
    }
}

