.pop-setting {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba($dark-page,.8);
  display: none;
  z-index: 999999;    
  .menu-setting {
    @include itemCenter();
    width: 500px;
    height: auto;
    background: $white;
    border-radius: 8px;
    overflow: hidden;
    @extend %Raleway-font;
    z-index: 9999999;
    @include m576 {
      width:70%;
    }
    .inner-box {
      padding-bottom: 40px;
      .head-title {
        background: $main;
        color:$white;
        padding: 15px 0;
        margin-bottom: 20px;
        position: relative;
        h2 {
          font-size: 18px;
          margin: 0;
          @extend %Poppins-font;
          display: inline-block;
        }
        a {
          color: $white;
          font-size: 22px;
          position: absolute;
          right: 15px;
        }
      }
      ul {
        margin: 0;
        li {
          font-size: 14px;
          margin-bottom: 15px;
          a {
            color: $gray-link;
            text-decoration: none;
            font-weight:600;
            @include trans(all,.3s,linear);
            &:hover {
              color:$main;
            }
          }
        }
      }
    }
  }

}
