.mobile-nav {
  @media (min-width: 993px) {
    display: none;
  }
  .navbar {
    padding: 5px 0 0;
  }
  .header {
    width: 100%;
    display: flex;
    margin-bottom: 15px;
    .menu {
      flex: 0 1 8%;
      position: relative;
      display: block;
      border-top: 3px solid $main;
      margin-top:12px;
      max-width: 30px;
      cursor: pointer;
      &:before,&:after {
        content: '';
        position: absolute;
        bottom: 15px;
        left: 0;
        width: 80%;
        height: 3px;
        background: $main;
        border-radius: 50px;
      }
      &:after {
        bottom: 8px;
        width: 60%;
      }
    }
    .navbar-brand {
      @extend %Luckiest-font;
      color: $main;
      font-size:24px;
      padding: 0;
      text-align: center;
      flex: 0 1 92%;
      margin: 0;
    }
  }
  ul {
    display: block;
    width: 100%;
    text-align: center;
    .nav-item {
      float: left;
      width: 16.666%;
        &.active {
            .nav-link {
                color: $main;
            }
        }
      .nav-link {
        color: $gray;
        font-size: 22px;
        outline: inherit;
        padding: 5px 0;
        position: relative;
        @include trans(all,.3s,linear);
          img {
            max-width:30px;
            border-radius: 50%;
          }
        .counts-notifications,.counts-message {
          @extend %Poppins-font;
          position: absolute;
          top: 25px;
          left: 60%;
          background: $main;
          min-width: 20px;
          max-width: 40px;
          padding: 0 8px;
          height: 15px;
          line-height: 15px;
          text-align: center;
          font-size: 12px;
          letter-spacing: 1px;
          color: #FFF;
          display: block;
          font-weight: bold;
          border-radius: 5px;
        }
        &:hover {
          color: $main;
        }
      }
    }
  }
}
