.sidebar {
  background: $dark;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  width: 0;
  color: $white;
  z-index: 999;
  .menu {
    overflow: hidden;
    padding: 80px 0;
  }
  & > i {
    position: absolute;
    right: -30px;
    top: 50%;
    background: $dark;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
  }
  ul {
    li {
      margin-bottom: 20px;
      width: 200px;
      overflow: hidden;
      background: $main;
      cursor: pointer;
      a {
        display: block;
        color: $white;
        text-decoration: none;
        i {
          width: 40px;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          text-align: center;
        }
      }
    }
  }
}
