.main-navbar {
  .navbar {
    padding: 5px 0;
    @include m992 {
      display: none;
    }
    .navbar-brand {
      @extend %Luckiest-font;
      color: $main;
      font-size: 30px;
      padding:0;
    }

    .nav-item {
      margin: 0 5px 0 0;
      &.active {
        .nav-link {
          color: $main;
        }
      }
      .nav-link {
        color: $gray;
        font-size: 12px;
        font-weight: bold;
        outline: inherit;
        position: relative;
        @include trans(all,.3s,linear);
          img {
            max-width: 27px;
          border-radius: 50%;
            margin-top: -5px;
            margin-right: 5px;
            position: absolute;          
            left: 0;
          }
        &:hover {
          color:$main;
        }
        .counts-notifications,.counts-message {
          @extend %Poppins-font;
          position: absolute;
          top: 0;
          left: 10px;
          background: $main;
          min-width: 20px;
          max-width: 40px;
          padding: 0 4px;
          height: 15px;
          line-height: 15px;
          text-align: center;
          font-size: 10px;
          color: $dark-page;
          display: block;
          font-weight: bold;
          border-radius: 50px;

        }
        i {
          margin-right: 5px;
        }
      }
    }

    .nav-item.dropdown {
      .dropdown-toggle {
        &::after {
          color: $main;
          @include trans(all,.2s,linear);
            margin-left: 25px;
        }
      }
      &.show {
        .dropdown-toggle {
          &::after {
            border-bottom: .3em solid;
            border-top: 0;
          }
        }
      }
      .dropdown-menu {
        min-width: 0;
        font-size: 12px;
        padding: 0;
        margin: 6px 0;
        overflow: hidden;
        .dropdown-item {
          @include trans(all,.2s,linear);
          font-weight: bold;
          &:active {
            background: $main;
          }
          &:hover {
            background: $main;
            color: $white;
          }
        }
      }
    }
  }
}
